.side-menu{
	margin-bottom:20px;

	.title{
		margin:0 0 5px;

		a{
			font-size:16px; font-weight:bold; text-transform:uppercase; text-decoration:none; color:@color-blue-dk;
		}
	}

	li{
		line-height:1.6em;

		> a {
			text-decoration:none; display:block; padding-left:20px; position:relative; color:@color-default;

			&:before{
				.ico-font();

				content:"\61"; position:absolute; top:5px; left:0; font-size:15px; height:14px; overflow:hidden; color:@color-default;

			}

			&:hover{
				text-decoration:underline;
			}
		}

		&.selected,
		&.active{
			> a{
				font-weight:bold; color:@color-blue-dk;
			}
		}
	}

	.level-2{
		margin-bottom:10px;

		> li{
			line-height:1.5em;

			> a {
				font-size:12px; padding-left:34px;

				&:before{
					font-size:11px; top:6px; left:20px; height:11px;

				}
			}
		}
	}

	.level-3{
		margin-bottom:10px;

		> li{
			line-height:1.5em;

			> a {
				font-size:12px; padding-left:44px;

				&:before{
					font-size:11px; top:6px; left:30px; height:11px;

				}
			}
		}
	}

	.level-4{
		margin-bottom:10px;

		> li{
			line-height:1.5em;

			> a {
				font-size:12px; padding-left:54px;

				&:before{
					font-size:11px; top:6px; left:40px; height:11px;

				}
			}
		}
	}

	.level-5{
		margin-bottom:10px;

		> li{
			line-height:1.5em;

			> a {
				font-size:12px; padding-left:64px;

				&:before{
					font-size:11px; top:6px; left:50px; height:11px;

				}
			}
		}
	}

	@media @w768-max{
		margin-bottom:10px;

		.title{
			margin:0;

			a{
				display:block; border:1px solid @color-border; border-radius:5px; padding:10px; position:relative;

				&:after{
					content:"\63"; font:normal 15px/1 @font-icons; .transition-transform (0.3s ease); text-transform:none;  display:inline-block; position:absolute; width:16px; height:15px; color:@color-gray; right:10px; top:50%; margin-top:-7px;
				}
			}
		}

		&.ca-expanded{
			.title a:after{
				.transform(~'rotate(180deg)');
			}
		}

		.menu{
			li{
				margin:8px 0;
			}
		}


	}
}