.category{
	overflow:hidden;

	.num{
		float:left; margin-left:-35px; text-align:right; width:29px; font-size:16px; color:@color-blue-dk; font-weight:bold; display:block; line-height:1.5;
	}

	ul{
		margin-top:-1px;
	}

	li{
		padding:10px 0 10px 35px; .cfx(); border-top:1px solid @color-border;

		a{
			text-decoration:none;
		}
	}

	.title{
		font-size:16px; margin:0 0 5px; text-decoration:underline;
	}

	.desc{
		margin-bottom:6px; overflow:hidden;
	}

	.section{
		font-size:13px; color:@color-gray; margin-bottom:0;

		a{
			text-decoration:underline;
		}
	}

	.img{
		margin:0 0 6px;

		img{
			.responsive-img(); max-height:60px;
		}
	}

	@media @w480{
		li{
			padding-top:15px; padding-bottom:20px;
		}

		.img{
			margin:0 15px 0 0; float:left; width:156px; text-align:center;

			img{
				max-height:100%;
			}
		}
	}

	@media @w768{

	}
}