.news-list{
	overflow:hidden; margin:@margin-default;

	> ul{
		margin-top:-20px;
	}

	.item{
		margin-top:20px;
	}

	.spc{
		text-decoration:none; color:@color-default; display:block; padding-left:90px;

		.cfx();

		&.no-img{
			padding-left:0;
		}
	}

	.desc{
		margin:0;

		p, ol, ul{
			margin-bottom:0.5em;

			&:last-child{
				margin-bottom:0;
			}
		}


	}

	.title{
		margin:0 0 3px; .transition(color 0.3s ease); font-size:16px; color:@color-link;
	}

	.date{
		color:@color-gray-lt; font-size:13px; font-weight:normal;
	}

	.img{
		float:left; width:80px; margin-left:-90px; text-align:center; margin-top:2px;

		img{
			.responsive-img();
		}
	}

	@media @w768{
		.spc:hover{
			.title{
				color:@color-link-hover;
			}
		}

		.title{
			font-size:20px;
		}

		.date{
			font-size:18px;
		}

		> ul{
			margin-top:-35px;
		}

		.item{
			margin-top:35px;
		}

		.desc{
			font-size:18px;
		}
	}
}

.more-news,
.more-info{
	@media @w768{
		font-size:18px;
	}
}
