#header {
	background:#f9f9f9; height:74px; .box-sizing(); border-top:4px solid #ededed; .box-sizing(); border-bottom:4px solid #303030; margin-bottom:20px;

	#logo {
		text-align:center; margin:0; font-size:100%; position:absolute; left:10px; top:8px;
	}

	img{
		height:54px; width:auto;
	}

	.hp &{
		margin-bottom:0;
	}

	@media @w480{
		border-bottom:none; height:70px; margin-bottom:0;
	}

	@media @w768 {
		height:133px;

		img{
			height:116px;
		}

	}
	@media (min-width: 1200px){
		#logo{
			margin-left:-585px; left:50%;
		}
	}
}

