/* MESSAGES */

.message {
	padding:10px; background:#fff; color:@color-default; border:1px solid; border-left-width:51px; text-align:left; position:relative; font-weight:bold;

	&:before{
		.ico-font();

		color:#fff; content:""; width:51px; position:absolute; top:50%; margin-top:-13px; left:-51px; text-align:center; font-size:26px;
	}

	&.error-message {
		color:@color-msg-error; border-color:@color-msg-error;

		&:extend(.icon-close all);

		&:before{
			margin-top:-10px; font-size:20px;
		}
	}
	&.ok-message {
		&:extend(.icon-check all);

		border-color:@color-msg-ok;

		&:before{
			background:@color-msg-ok;
		}
	}
	&.info-message {
		&:extend(.icon-info all);

		border-color:@color-msg-info;
	}

	@media @w960 {
		padding:13px 15px;
	}
}



