#main-menu{
	@media @w768-max{
		.ca-control {
			right:0;

			a{
				.title{
					.vhide();
				}

				&:before { content:"\62";  font:48px/50px @font-icons; margin:-24px 0 0 -24px;}
			}
		}

		.ca-box {
			max-width:300px; left:auto; right:0; padding:0;
		}

		li{
			a{
				display:block; border-top:1px solid @color-border; text-transform:uppercase; .anchor-color(#fff; #fff); .transition(background 0.3s ease); font-weight:bold; text-decoration:none; padding:15px 20px 15px 20px;

				&:hover{
					background:#444;
				}
			}

			&:first-child{
				a{
					border-top:none;
				}
			}
		}
	}
	@media @w768{
		position:absolute; right:10px; top:77px;

		.ca-control{
			.vhide();
		}
		.menu{
			ul{
				.inline-block();
			}

			li{
				margin-left:2px;

				&.selected a,
				a:hover{
					background:@color-blue; color:#fff;
				}
			}
			a{
				display:block; padding:0 10px; line-height:36px; text-decoration:none; text-transform:uppercase; font-weight:bold; color:@color-blue; border-radius:5px; font-size:13px;

				.transition(~"background 0.3s ease, color 0.3s ease");
 			}
		}
	}

	@media (min-width: 1200px){
		margin-right:-585px; right:50%;

		.menu{
			a{
				padding:0 13px;
			}
		}
	}
}








