#control-up{
	position:fixed; right:0; bottom:0; z-index:21; margin-bottom:0;

	a{
		width:60px; height:60px; color:#fff; position: relative; display:block; background:@color-blue; border-radius:5px 5px 0 0; opacity:0; .transition(opacity 1s ease);

		&:before{position:absolute; top:50%; margin:-15px 0 0 -15px; left:50%; font-size:30px; }
	}

	@media (min-width:1470px){
		right:50%; margin:0 -732px 0 0;
	}
}

.with-up #control-up a{opacity:1;}

