/************************************************************************************
	RESETS and TYPOGRAPHY
*************************************************************************************/
html {
	.mp-reset;
}
body {
	.mp-reset;
	background:#fff; font:100%/@lh-default @font-default; .font-size(@font-base-size-mobile; 16); color:@color-default; text-align:center; -webkit-font-smoothing:antialiased; -webkit-text-size-adjust:none;

	@media @w768{
		.font-size(@font-base-size; 16);
	}
}

/* HEADING */
h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, h6 { margin:.9em 0 .5em; padding:0; font-weight:bold; line-height:1.5; font-family:@font-default; color:@color-blue-dk;
	a & { color:inherit; }
}
// media uvnitř selectoru zajistí, že pokud někde v použiju konstrukci h2 { .h1 }, budou velikosti správné při každém rozlišení
h1, .h1 {
	.font-size(20; @font-base-size-mobile); margin-top:0;
	@media @w768 { .font-size(24); }
	@media @w960 { .font-size(30); }
}
h2, .h2 {
	.font-size(18; @font-base-size-mobile);
	@media @w768 { .font-size(19); }
	@media @w960 { .font-size(20); }
}
h3, .h3 {

	.font-size(15; @font-base-size-mobile);
	@media @w768 { .font-size(16); }
	@media @w960 { .font-size(16); }
}
h4, .h4, h5, h6 {
	font-size:100%;

	@media @w960 {
		.font-size(14);
	}
}


/* FONTS */
.bold { font-weight:bold; }
.thin { font-weight:normal; }

.small { .font-size(13); }
.big {
	.font-size(15; @font-base-size-mobile);
	@media @w768 { .font-size(16); }
	@media @w960 { .font-size(18); }
}

.center { text-align:center; }
.left   { text-align:left; }
.right  { text-align:right; }

.vcenter { vertical-align:middle; }

.uppercase { text-transform:uppercase; }

.gray { color:@color-gray; }
.black { color:@color-dark; }
.orange { color:@color-orange; }
.white {color:#fff;}
.blue{color:@color-blue;}

/* RESET */
.mp-reset { margin:0; padding:0; }
.mt-reset { margin-top:0 !important; }
.mb-reset { margin-bottom:0 !important; }
.reset {
	border:none; background:none; .mp-reset;

	ul{
		margin-top:0;
	}
}
.reset > li {
	.reset(); // () musí být, jinak se pokazí selektor s before!
	position:static;

	&:before { display:none; position:static; top:auto; width:auto; height:auto; margin:0; border-radius:0; background:none; }
}
table.reset { border-spacing:0; }
table.reset, table.reset td, table.reset th, table.reset thead th { width:auto; .reset; font-weight:normal; color:inherit; }
table.reset tr:nth-child(even) th, table.reset tr:nth-child(even) td { background:none; }

/* MISC */
.out { position:absolute; left:-10000px; top:auto; }
.vhide { border:0; clip:rect(0 0 0 0); height:1px; margin:-1px; overflow:hidden; padding:0; position:absolute; width:1px; }
.l { float:left; }
.r { float:right; }
.cb { clear:both; }
.nobr { white-space:nowrap; }
.print-only,
.no-js .js-only,
.js .js-hide,
.hidden { display:none; }
.break { height:0; font-size:0; line-height:0; clear:both; overflow:hidden; visibility:hidden; display:block; margin:0 !important; padding:0 !important; width:100%; }
.cfx() {
	&:before, &:after { content: " "; display: table; }
	&:after { clear: both; }
}

.cfx, form .row{
	.cfx();
}

/* PARAGRAPH */
div, pre { .mp-reset; }
p {
	margin:@margin-default;
	line-height:@lh-default;
}
hr { margin:20px 0; padding:0; border:solid @color-border; border-width:1px 0 0; color:@color-border; line-height:0; height:0; overflow:hidden; }
pre { .left; }
em { font-family:@font-default; }
strong{ font-weight:bold; }
sup {
  position: relative;
  top: -4px;
  line-height: normal;
}

/* CITE */
cite { border:none; }
blockquote {
	background:@color-gray-lt; color:@color-dark; border:none; padding:15px 15px 10px 15px; margin:@margin-default;

	p {
		margin:0 0 .5em;
	}

	@media @w768{
		padding:30px 30px 20px 30px;
	}
}

/* ANCHOR */
a {
	color:@color-link; text-decoration:underline; outline:none;

	&:hover, &:active { color:@color-link-hover; text-decoration:underline; }

	@media @w590 {
		.transition(color .3s ease);
	}

	.no-touchevents &{
		&[href^="tel:"]{
			text-decoration:none; color:@color-default;
		}
	}
}

/* LIST */
#nette-debug{
	ul{
		li{
			&:before{display:none;}
		}
	}
}

ul {
	margin:@margin-default; padding:0;

	ul { margin:.8em 0 0; }

	> li {
		position:relative; margin:0 0 .1em; padding:0 0 0 16px; line-height:@lh-default; list-style:none;

		&:before { position:absolute; top:.7em; left:2px; display:block; width:6px; height:6px; border-radius:50%; content:""; background:@color-blue; }
	}
}
ol{
	padding:0; margin:@margin-default;

	li{
		list-style-position:outside; margin:0 0 0.4em 20px; line-height:@lh-default;
	}
}

/* Google Map, YouTube video */
.video,
.iframe-wrap {
	position:relative; margin:@margin-default; height:0; padding-top:56.25%; background:@color-gray;

	&.reset { margin:0; }

	iframe { position:absolute; top:0; left:0; width:100%; height:100%; border:0; }
}


/* IMAGES */
img {
	border:0; vertical-align:middle; -ms-interpolation-mode:bicubic;

	// alt
	font:normal 14px/1.4 @font-default; text-decoration:none; color:@color-default; text-align:left;
	&:before { display:inline-block; line-height:1.4; }

	&.border{
		border:1px solid @color-border;
	}
}
img.left{ margin-bottom:1em;}
img.right{ margin-bottom:1em;}

/* #content styles */
#content,
.wysiwyg{
	img{.responsive-img(); min-width:30px;}
}

/* TABLE */
table {
	margin:@margin-default; .font-size(13; @font-base-size-mobile); border:0; border-spacing: 0; border-collapse:collapse; padding:0; width:100%; font-family:@font-default;

	#content &{
		margin-top:0.8em; margin-bottom:2em;
	}
}
td, th { padding:.6em 4px; border:solid @color-border; border-width:0 0 1px; text-align:left; background:none; color:@color-default; font-weight:normal; vertical-align:top; }
thead th { background:@color-blue; color:#fff; font-weight:bold;}

.middle-tbody tbody,
.middle-thead thead{
	td, th{
		vertical-align:middle;
	}
}

@media @w590 {

	.col-h-l,
	.col-h-r{
		width:48%; display:block; float:left;
	}
	.col-h-r{float:right;}

	.col-h-r .col-h-l,
	.col-h-l .col-h-l,
	.col-h-r .col-h-r,
	.col-h-l .col-h-r{ float:none; width:auto; }

	img.left{ float:left; margin:0 20px 15px 0; clear:both; }
	img.right{ float:right; margin:0 0 15px 20px; clear:both; }

	#content iframe,
	#content embed,
	#content object{max-height:none;}

	.mobile-only{
		display:none;
	}
}

@media @w768-max {
	.col-h-l, .col-h-r {
		&.m-full { float:none; width:auto; }
	}
}

@media @w768{
	.col-h-r .col-h-l,
	.col-h-l .col-h-l{ float:left; width:48%; }
	.col-h-r .col-h-r,
	.col-h-l .col-h-r{ float:right; width:48%; }

	table{
		.font-size(14; @font-base-size-mobile);
	}

	th{padding:.8em 10px;}
	td{padding:.45em 10px;}
}
