.hp-carousel{
	margin:@margin-default;

	.items{
		position:relative;

		&:hover{
			.prev, .next{
				opacity:0.5;

				&:hover{
					opacity:1;
				}
			}
			.pager{
				opacity:0.5;

				&:hover{
					opacity:1;
				}
			}
		}
	}

	ul{white-space:nowrap; .inline-block();}

	li{
		white-space:normal; letter-spacing:normal;
	}

	.prev,
	.next{
		width:0; height:0; text-decoration:none; opacity:0; .transition(opacity 0.3s ease); overflow:hidden; padding:40px 40px 0 0; z-index:2; .box-sizing(); border:1px solid #fff; border-radius:1px; position:absolute; top:50%; margin-top:-20px;

		&:before{
			position:absolute; left:50%; top:50%; margin-top:-10px; margin-left:-10px; font-size:20px; color:#fff;
		}
	}

	.prev{
		left:10px;
	}
	.next{
		right:10px;
	}

	.pager{
		position:absolute; text-align:right; left:0; right:40px; top:20px; z-index:2; margin:0 auto; opacity:0; .transition(opacity 0.3s ease);

		a{
			width: 0; height:0; border-radius:1px; padding:20px 20px 0 0; .transition(~"background-color 0.3s ease, border-color 0.3s ease"); display:inline-block; vertical-align:middle; margin:0 10px; overflow:hidden; border:1px solid #fff;

			&:hover{
				background:#fff;

			}

			&.selected{
				background:@color-blue;
				border-color:@color-blue;
			}
		}

		@media @w768-max{
			top:-5000px;
		}
	}

	.content{
		text-align:center; width:40%; max-width:500px; position:absolute; top:0; bottom:0; left:0; white-space: nowrap;

		&:before{
			content: '';
			display: inline-block;
			height: 100%;
			vertical-align: middle;
			margin-right: -0.25em;
		}

		.inner{
			display:inline-block; vertical-align:middle; white-space:normal;
		}
	}

	.style1{
		font-size:12px;margin:0; line-height:1.3;
	}
	.style2{
		font-size:4.3vw; font-weight:bold; margin:0; line-height:1.3;
	}
	.style3{
		font-size:11px;margin:0; line-height:1.3;
	}
	.style-default{
		font-size:11px; margin:0; line-height:1.3; text-transform:uppercase; font-weight:bold;
	}

	.img{
		background:#000; text-align:center; margin-bottom:0;

		img{
			.responsive-img(); width:100%;
		}
	}
	.bottom{
		background:#191919; padding:15px 0; color:#fff;

		.in-box{
			text-align:center;
		}
	}
	.text{
		font-size:17px; margin-bottom:10px;

		.small{font-size:14px;}
	}
	.button{
		margin:0;

		.btn{
			vertical-align:middle; margin-right:10px;
		}
	}
	.spc{
		text-decoration:none; color:#fff; position:relative; display:block;
	}

	@media @w480{
		.style-default{
			font-size:1.6vw; line-height:2;
		}
	}

	@media @w768{
		.prev{
			left:50px;
		}
		.next{
			right:50px;
		}

		.style1{
			display:inline-block;

			&:before,
			&:after{
				width:40px; height:10px; margin-top:-2px; content:""; vertical-align:middle; display:inline-block; background:url('data:image/png;base64,') 0 0;
			}

			&:before{
				margin-right:10px;
			}
			&:after{
				margin-left:10px;
			}
		}
		.style1{
			font-size:1.6vw;
		}
		.style3{
			font-size:1.3vw;
		}
		.style-default{
			font-size:1.4vw;
		}
	}

	@media @w1190{
		.bottom{
			padding:25px 0;

			.in-box{
				.cfx(); text-align:left;
			}
		}
		.text{
			float:left; margin-bottom:0; line-height:41px;
		}
		.button{
			float:right;
		}

		.content{
			left:50%; margin-left:-585px;
		}
	}

}