.product-detail{
	.product-img{
		a{
			border: 1px solid @color-border; padding:5px; display:inline-block; vertical-align:top; position:relative;

			&:before{
				position:absolute; top:0; left:0; right:0; bottom:0; background:rgba(255,255,255,0.5); content:""; opacity:0; .transition(opacity 0.3s ease);
			}

			&:hover{
				&:before{
					opacity:1;
				}
			}
		}

		img{
			.responsive-img();
		}

		@media @w480{
			float:right; width:140px; margin:0 0 1.3em 20px;
		}

		@media @w960{
			width:250px;
		}
	}
}






