.app-list{
	overflow:hidden; margin: @margin-default;

	ul{
		margin-top:-5px;
	}

	li{
		margin-top:5px;
	}
	.spc{
		display:block; position:relative; color:#fff; text-decoration:none;

		&:after{
			position:absolute; top:0; left:0; z-index:2; right:0; bottom:0; background:rgba(0,0,0,0.3); content:""; opacity:0; .transition(opacity 0.3s ease);
		}

		&:hover{
			&:after{
				opacity:1;
			}

			.title{
				margin-bottom:10px;
			}
		}
	}

	.title{
		font-size:22px; margin:0; text-transform:uppercase; z-index:3; position:absolute; left:0; right:0; bottom:20px; text-align:center;

		.transition(margin-bottom 0.3s ease);
	}

	.img{
		margin:0; position:relative; z-index:1;

		&:after{
			position: absolute;  left: 0;  right: 0;  bottom: 0;  margin: auto;  content: ""; z-index: 1;  top: 30%;
			background: -moz-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.49) 100%);
			background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.49) 100%);
			background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.49) 100%);
			filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00000000', endColorstr='#7d000000', GradientType=0);
		}
	}
	img{
		.responsive-img(); width:100%;
	}

	@media @w590{
		.grid-list(2);

		margin-left:-5px; margin-right:-5px;

		ul{
			margin-top:-10px;
		}

		li{
			.box-sizing(); padding:0 5px; margin-top:10px;
		}
	}

	@media @w768{
		margin-left:-10px; margin-right:-10px;

		ul{
			margin-top:-20px;
		}

		li{
			padding:0 10px; margin-top:20px;
		}

		.title{
			font-size:30px; bottom:30px;
		}
	}

	@media @w960{

	}

	@media @w1190{
		.title{
			font-size:40px;
		}
	}
}