.breadcrumb {
	display:none;

	@media @w480 {
		display:block; .font-size(13); background:#303030; box-shadow:0 3px 5px rgba(0,0,0,0.3); margin-bottom:20px;

		.in-box{
			padding-top:13px; padding-bottom:14px;
		}

		a{
			.anchor-color(#fff; @color-link-hover); display:inline-block; vertical-align:top; margin:0 36px 0 0; position:relative;

			&:after{
				.ico-font; content:"\61"; font-size:16px; color:@color-gray; position:absolute; right:-27px; top:3px;  height:15px; overflow:hidden;
			}
		}

		strong{
			font-weight:normal; color:@color-blue;
		}

		&.empty{
			margin:0; box-shadow:none; height:4px;


		}
	}

	@media @w768{
		margin-bottom:30px;
	}

}
