/* HEADER BOXY */
@import "box/header/search-box";
@import "box/header/lang-box";
@import "box/header/header-other-tools";



.header-box {
	@media @w768-max{
		.ca-control {
			position:absolute; top:0; width:70px; .box-sizing(); margin:0; z-index:4;

			a {
				display:block; height:70px; .box-sizing(); font-size:13px; font-weight:300; position:relative; text-decoration:none; color:@color-default;

				.transition(~"color 0.3s ease, background 0.3s ease");

				&:before { font:30px/31px @font-icons; position:absolute; left:50%; margin:-15px 0 0 -15px; top:50%; text-transform:none;}
			}
		}

		.ca-box {
			position:absolute; top:70px; padding:20px; box-shadow:0 3px 5px rgba(0,0,0,0.3); z-index:2; left:0; display:block; width:100%; margin:0; .box-sizing(); opacity:0; visibility:hidden; background:#555; text-align:left;

			.transition(~"opacity 0.3s ease, visibility 0s linear 0.3s");
		}

		&.ca-expanded {

			.ca-control{
				a{
					background:#555; color:#fff;
				}
			}

			.ca-box{
				opacity:1; visibility:visible;

				.transition(~"opacity 0.3s ease, visibility 0s linear 0s");
			}
		}
	}

}

