	@media @w768-max{
		.ca-control {
			right:140px;

			a{
				.title{
					font-weight:normal; font-size:16px; text-align:center; line-height:70px;
				}

				&:before { display:none;  }
			}
		}

		.ca-box {
			max-width:70px; left:auto; right:140px; padding:0;
		}

		li{
			a{
				display:block; border-top:1px solid @color-border; text-transform:uppercase; .anchor-color(#fff; #fff); .transition(background 0.3s ease); font-weight:bold; text-decoration:none; padding:15px 5px 15px 15px;

				&:hover{
					background:#444;
				}
			}

			&:first-child{
				a{
					border-top:none;
				}
			}

			[class^="flag-"]:before,
			[class*=" flag-"]:before {
				margin-top:-2px;
			}
		}


	}

	@media @w768{
		right:300px; position:absolute; top:22px;

		.ca-control {
			margin:0; line-height:20px;

			a{
				display:block; padding-right:14px; position:relative; color:@color-gray; text-decoration:none;

				.title{
					font-weight:normal; font-size:13px;
				}

				&:after{
					.ico-font();

					content:"\63"; position:absolute; right:0; top:8px; font-size:10px;
				}
			}

			[class^="flag-"]:before,
			[class*=" flag-"]:before {
				margin-top:-3px;
			}
		}

		li{
			a{
				display:block; border-top:1px solid @color-gray-lt; font-size:13px; text-transform:uppercase; .anchor-color(@color-gray; @color-blue); text-decoration:none; padding:10px 0 10px 10px;
			}

			&:first-child{
				a{
					border-top:none;
				}
			}
		}

		.ca-box {
			position:absolute;  box-shadow:0 4px 3px rgba(0,0,0,0.3); z-index:2; display:block; top:27px; left:-10px; margin:0; .box-sizing(); right:-10px; opacity:0; visibility:hidden; text-align:left; background:#fff;

			.transition(~"opacity 0.3s ease, visibility 0s linear 0.3s");

			padding:0;
		}

		&.ca-expanded {

			.ca-box{
				opacity:1; visibility:visible;

				.transition(~"opacity 0.3s ease, visibility 0s linear 0s");
			}
		}
	}

	@media (min-width: 1200px){
		margin-right:-585px; right:50%;
	}



