#footer{
	margin-top:60px;

	.hp &{
		margin-top:0;
	}

	.footer-black{
		background:#303030; color:@color-gray-lt; position:relative;

		.in-box{
			 padding-bottom:10px; padding-top:30px; position:relative;
		}
	}

	h2{
		margin:0; padding:10px; text-align:center; font-weight:normal; position:absolute; background:@color-blue; color:#fff; display:inline-block; top:-22px; left:10px; right:10px;
	}

	.inputs{
		position:relative; padding-right:100px; max-width:480px; .box-sizing(); margin:0 auto;

		.inp-text{
			border:none; border-radius:1px 0 0 1px;
		}

		.btn{
			width:100px; position:absolute; top:0; right:0; padding:0; border-radius:0 1px 1px 0;
		}

		@media @w960{
			margin:8px 0 0 0;
		}
	}

	.ca-control{
		color:#fff; font-weight:normal;
	}

	a{
		.anchor-color(#fff; @color-gray-lt);
	}

	.copy{
		margin-bottom:10px; font-size:13px;
	}
	.author{
		margin:0; font-size:13px;

		img{
			margin-left:5px; margin-right:5px;
		}
	}

	.footer-gray{
		background:#e6e6e6; color:@color-gray;

		.in-box{
			padding-top:10px; padding-bottom:10px;
		}

		.cfx();

		a.gray{
			.anchor-color(@color-gray; @color-dark);
		}
		a.orange{
			.anchor-color(@color-orange; @color-dark);
		}
	}

	@media @w480{
		h2{
			left:50%; width:440px; margin-left:-220px; right:auto; .box-sizing();
		}
	}

	@media @w768-max{
		.in-box{
			text-align:center;
		}

		.ca-box{display:none;}

		.ca-control{
			margin:0; padding:13px 10px;

			a{
				text-decoration:none; display:block; padding:13px 10px; margin:-13px -10px;

				&:after{
					content:"\63"; font:normal 15px/1 @font-icons; .transition-transform (0.3s ease);  display:inline-block; vertical-align:middle; margin-left:10px; width:16px; height:18px; color:#fff;
				}
			}
		}

		.ca-expanded{
			.ca-control a:after{
				.transform(~'rotate(180deg)'); margin-top:-2px;
			}
		}

		li{
			a{
				display:block; padding:5px;
			}
		}
	}

	@media @w768{
		h2{
			left:10px; margin-left:auto; width:auto;
		}

		.ca-box{
			display:block !important; height:auto !important;
		}

		.grid-item-nl .ca-box{
			max-width:320px;
		}

		.ca-control{
			margin:0 0 10px; border-bottom:2px solid @color-gray-lt; padding-bottom:10px;

			a{
				text-decoration:none; cursor:text; .anchor-color(#fff; #fff);
			}
		}

		.footer-black{
			.in-box{
				 padding-bottom:40px; padding-top:50px;
			}
		}

		li{
			margin-bottom:.5em;
		}

		.footer-cols{
			margin:0 -10px;

			.grid-list(2);

			.grid-item{
				.box-sizing(); padding:0 10px;

				&.grid-item-contact{
					display:block; width:auto; padding-right:20px;
				}
			}
		}

		.copy{
			float:left; margin-bottom:0;
		}

		.author{
			float:right;
		}

		.footer-gray{
			.in-box{
				.cfx();
			}
		}

		.address,
		.numbers,
		.other{
			display:inline-block; vertical-align:top;
		}
		.numbers{
			margin-left:40px; margin-right:40px;
		}
	}

	@media @w1190{
		.footer-cols{
			margin:0 -15px;

			> .grid > .grid-item{
				.box-sizing(); padding:0 15px; width:430/((1200 * 1.01)/100%);

				&.grid-item-contact{
					display:inline-block; width:430/((1200 * 1.01)/100%); padding-right:15px;
				}

				&.grid-item-nav{
					width:340/((1200 * 1.01)/100%);
				}
			}
		}
		.numbers{
			margin-left:20px; margin-right:0;

			.version-ro &{
				margin-left:0;
			}
		}
	}



}
