#search-box {
	.inputs{
		position:relative; padding-right:80px; max-width:480px; .box-sizing(); margin:0 0 0 auto; z-index:2;

		.inp-text{
			border-radius:1px 0 0 1px;
		}

		.btn{
			width:80px; position:absolute; top:0; right:0; padding:0; border-radius:0 1px 1px 0;
		}
	}


	@media @w768-max{
		.ca-control {
			right:70px;

			a{
				.title{
					.vhide();
				}

				&:before { content:"\65";  }
			}
		}

		.btn{
			background:@color-blue;

			&:hover, &:focus, &:active {background-color:darken(@color-blue, 10%);}
		}
	}

	@media @w768{
		position:absolute; top:16px; right:10px;

		.inputs{
			max-width:none; width:270px;
		}

		.ca-control{
			.vhide();
		}
	}

	@media (min-width: 1200px){
		margin-left:-335px; left:50%; right:auto; top:48px;
	}




}


