/**
 * Výpisy produktů:
 *   .product-list
 */

.product-list{
	overflow:hidden;

	ul{
		margin-top:-4px;
	}

	li{
		margin-top:4px;
	}

	.spc{
		display:block; background:#f8f9fd; padding:0 10px 15px; border:1px solid @color-border; border-radius:10px; overflow:hidden; text-decoration:none; color:@color-default;
	}
	.title{
		margin:0; font-size:14px; color:@color-blue-dk;

		.transition(color 0.3s ease);
	}

	.desc{
		font-size:13px; margin-bottom:0;
	}

	.img{
		margin:0 -10px 10px;

		img{
			.responsive-img(); width:100%;
		}
	}

	@media @w480{
		.grid-list(2);

		margin:0 -2px;

		li{
			.box-sizing(); padding:0 2px;
		}

		.img{
			max-height:144px; overflow:hidden;
		}


	}


	@media @w590{
		.grid-list-set-items(3);
	}

	@media @w768{
		margin:-4px -2px;

		li{
			padding:4px 2px;
		}

		.spc{
			.transition(box-shadow 0.3s ease);

			&:hover{
				box-shadow:0 2px 3px rgba(0,0,0,.22);

				.title{
					color:@color-blue;
				}
			}
		}
		.desc{
			font-size:12px;
		}
	}

	@media @w960{
		margin:-4px -10px -4px -9px;

		ul{
			margin-top:-8px;
		}

		li{
			padding:4px 10px 4px 9px; margin-top:12px;
		}
	}

	@media @w1190{
		.grid-list-set-items(4);
	}


}



