.btn {
	position:relative; display:inline-block; max-width:100%; height:34px; padding:0 10px; .box-sizing(); background:@color-blue; border:none; font:normal bold 14px/34px @font-default; text-align:center; color:#fff; vertical-align:top; font-weight:bold; border-radius:1px; text-decoration:none; text-transform:uppercase;
	.transition(~'background-color 300ms ease');

	&:hover, &:focus, &:active { outline:none; background-color:darken(@color-blue, 10%); text-decoration:none; color:#fff; }

	br { display:none; }
	span { display:inline-block; max-width:100%; vertical-align:top; line-height:34px; }

	@media @w480{
		padding:0 20px;
	}
}


/* Barvy */
.btn-dark{
	background:#4b4b4b;

	&:hover, &:focus, &:active {background-color:darken(#4b4b4b, 10%);}
}

/* Rozměry */
@media @w480{
	.btn-l{
		height:44px; line-height:44px; font-size:16px;

		span { line-height:44px; }

		@media @w768 {
			padding:0 40px;
		}
	}
}

.btn-full-width { width:100%; }


