@charset "UTF-8";

// FONT FACE
@font-face {
	font-family:'petruzalek';
	src:url('../webfonts/petruzalek.eot');
	src:url('../webfonts/petruzalek.eot?#iefix') format('embedded-opentype'),
		url('../webfonts/petruzalek.woff') format('woff'),
		url('../webfonts/petruzalek.ttf') format('truetype');
	font-weight:normal;
	font-style:normal;
}

@font-icons:'petruzalek';
@font-sans: 'Helvetica Neue', Arial, Helvetica, sans-serif;
@font-open: 'Open Sans', @font-sans;
@font-default: @font-open;
@font-base-size:14; // základní velikost písma
@font-base-size-mobile:14; // základní velikost písma na mobilu (<768)

@margin-default: 0 0 1.4em 0;
@lh-default: 1.6;

// MEDIA QUERIES
@w480:~'(min-width: 480px)';
@w590:~'(min-width: 590px)';
@w768:~'(min-width: 768px)';
@w960:~'(min-width: 960px)';
@w1190:~'(min-width: 1190px)';

@w480-max:~'(max-width: 479px)';
@w590-max:~'(max-width: 589px)';
@w768-max:~'(max-width: 767px)';
@w960-max:~'(max-width: 959px)';
@w1190-max:~'(max-width: 1189px)';

@retina:~'(-webkit-min-device-pixel-ratio:1.5), (min-device-pixel-ratio:1.5), (min-resolution: 144dpi)';

// COLORS
@color-default: @color-gray-dk;
@color-link:@color-blue;
@color-link-hover: @color-blue-dk;

@color-green:#2f7f24;
@color-dark: #000;
@color-gray-dk: #3d3d3d;
@color-gray: #818181;
@color-gray-lt: #cdcdcd;
@color-red:#be1522;
@color-blue: #01a0e4;
@color-blue-dk: #005caa;
@color-orange: #e44e25;

@color-border: @color-gray-lt;
@color-msg-error:@color-red;
@color-msg-ok:@color-green;
@color-msg-info:@color-blue;


// MISC
@hover-anim-delay:100ms;
@hover-anim-speed:300ms;

// IMPORT
@import "normalize.css";

@import "core/core";
@import "layout/layout";
@import "form/form";
