.header-other-tools{
	display:none;

	@media @w768{
		display:block; position:absolute; right:370px; top:22px; color:@color-gray;

		ul{
			.inline-block();
		}

		li{
			line-height:24px; margin-left:20px; font-size:13px;
		}

		[class^="icon-"],
		[class*=" icon-"]{
			position:relative; padding-left:17px;

			&:before{
				color:@color-gray; position:absolute; left:0; top:3px; font-size:15px;  height:14px; overflow:hidden;
			}
		}

		a.icon-phone[href^="tel:"]{
			color:@color-gray;
		}
	}

	@media (min-width: 1200px){
		margin-right:-505px; right:50%;
	}
}


