@import "normalize";
@import "buttons";
@import "inputs";
@import "messages";

/* FORM */
.form
{
	background:#f4f4f4; padding:40px 10px 10px; margin-bottom:10px; margin-top:40px; border:1px solid @color-border; position:relative;

	&.reset{
		background:none; padding:0; margin-bottom:0;
	}

	label.item{
		display:block; margin-bottom:10px;
	}

	h2{
		margin-top:0; font-size:18px; position:absolute; font-weight:normal; height:40px; display:inline-block; line-height:40px; padding:0 20px; background:@color-blue; color:#fff; top:-20px;
	}

	.btns{
		margin-bottom:0;
	}

	@media @w480{
		label.item{
			margin:0 20px 10px 0; white-space:nowrap; display:inline-block;
		}

		.btns{
			text-align:right;
		}
	}

	@media @w768{
		padding:40px 20px 20px; margin-bottom:20px;

		&.reset{
			background:none; padding:0; margin-bottom:0;
		}
	}
}

.filter-form{
	@media @w960{
		padding-right:160px; position:relative; padding-bottom:10px;

		.btns{
			position:absolute; right:20px; bottom:20px;
		}
	}
}

form p{margin-bottom:0.7em;}
form p.reset { margin-bottom:0; }

label, .label {
	font-weight:normal; cursor:pointer;

	.font-size(14; @font-base-size-mobile);
	@media @w768 { .font-size(14); }
}

/* INPUT ERRORS / NOTES / HELP */
.error label { color:@color-msg-error; }


