.company{
	border-top:5px solid #f0f0f0; font-size:16px; background:#f6f6f6 url("../img/bg/company.png") 50% 0 no-repeat;

	.in-box{
		text-align:center; padding-top:20px; padding-bottom:30px; min-height:280px; .box-sizing();
	}

	.big{
		font-weight:300; font-size:20px;
	}

	@media @w768{
		font-size:20px;

		.big{font-size:30px; margin-bottom:15px;}

	}
	@media @w960{
		font-size:24px;

		.big{font-size:40px;}

		.in-box{
			padding-top:35px;
		}
	}
}

