/* NAVIGATION SKIP */
#nav-skip {
	height:0; margin:0;
	a { position:absolute; left:-2000px; top:auto; }
	a:focus, #nav-skip a:active { position:absolute; left:0; width:250px; background:#fff; padding:0 0 5px; z-index:99; }
}

/* LAYOUT */
#outer-wrap { position:relative; overflow:hidden; width:100%; }

@padding:10px;

.in-box{
	padding:0 @padding; max-width:1190px; margin:0 auto; text-align:left; .box-sizing();

	&.no-padding{
		padding-left:0; padding-right:0;
	}
}

@media @w768{
	@padding:10px;

	.cols{
		.cfx(); padding-left:250px;
	}
	.col-side{
		width:240px; float:left; margin-left:-250px;
	}
	.col-content{
		width:100%; float:right; .box-sizing();
	}
}







